// кнопка
.btn {
    position: relative;
    cursor: pointer;
    border: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    text-align: center;
    font-family: $acrom;
    padding: 5px 20px;
    height: 40px;
    letter-spacing: 0.69px;
    background: #22415e;
    color: #fff;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 500;
    $parent: &;
    transition: all .1s $anim;

    &:hover {
        color: #fff;
        background: darken(#22415e, 5%);
    }

    &:active {
        transform: scale(0.96);
    }

    svg,
    img {
        width: 13px;
        height: 14px;
        fill: #fff;

        &:first-child {
            margin-right: 10px;
        }

        &:last-child {
            margin-left: 10px;
        }
    }

    &--icon {
        padding-left: 0 !important;
        padding-right: 0 !important;
    }

    &--red {
        background-color: $c10;

        &:hover {
            background: darken($c10, 5%);
        }
    }

    &--line-red {
        background: transparent;
        border: solid 1px #bc072a;
        color: #000;

        &:hover {
            background: #bc072a;
            color: #fff;
        }
    }

    &--line-gray {
        background: transparent;
        border: solid 1px #afafaf;
        color: #000;

        &:hover {
            background: #444;
            border-color: #444;
            color: #fff;
        }
    }

    &--full {
        width: 100%;
    }

    &.disabled {
        background-color: #ccc !important;
        background-image: none;
        opacity: 0.4;
        user-select: none;
        cursor: default;
        pointer-events: none;
    }

    @include media-breakpoint-down(sm) {
        letter-spacing: 0.5px !important;
    }
}