.modal { position: fixed; top: 0; left: 0; right: 0; bottom: 0; z-index: 99999; align-items: center; justify-content: center; flex-direction: column; background: rgba(0, 0, 0, 0.8); background-size: cover; transition: opacity 0.3s linear; opacity: 0; visibility: hidden; padding: 0 70px; overflow: auto; flex-flow: row wrap; display: none; }

