.menu-button { width: 30px; height: 23px; display: block; position: relative; margin: 0px; transform: rotate(0deg); transition: .5s ease-in-out; cursor: pointer; }

.menu-button span { display: block; position: absolute; height: 2px; width: 100%; border-radius: 9px; opacity: 1; left: 0; transform: rotate(0deg); transition: .25s ease-in-out; background: #000; }

