.poster-block__icon img { width: 60px; display: block; margin: 0 auto; }

.poster-block__title { font-size: 20px; font-weight: 500; }

.poster-block__time { font-size: 23px; color: #bc072a; font-weight: 500; }

.poster-block__date { font-size: 15px; font-weight: 500; }

.poster-module .poster-block { margin-bottom: 7px; }

.poster-module .btn { width: 100%; font-size: 24px; height: 60px; }

.tickets__title { text-transform: uppercase; color: #22415e; font-size: 15px; font-weight: 500; font-family: "Acrom"; }

.tickets li { display: flex; justify-content: space-between; align-items: center; margin-bottom: 15px; }

.tickets li:last-child { margin-bottom: 0; }

.tickets li img { display: block; max-width: 100%; flex-shrink: 0; margin-left: 15px; }

@media (max-width: 767px) { .tickets li img { max-width: 90px !important; } }

@media (max-width: 767px) { .tickets li { flex-wrap: wrap; } }

.tickets__price { color: #bc072a; font-size: 28px; font-weight: 700; font-family: "Acrom"; text-transform: uppercase; position: relative; padding-left: 30px; }

.tickets__price:before { content: ""; width: 15px; height: 1px; background: #bc072a; display: block; position: absolute; right: 100%; top: 50%; transform: translateY(-50%) translateX(100%); }

@media (max-width: 767px) { .tickets__price { font-size: 16px; } }

.poster-intro__image img { display: block; max-width: 100%; }

.poster-intro__bottom { display: flex; padding-right: 40px; margin-bottom: -60px; }

@media (max-width: 1160px) { .poster-intro__bottom { margin-bottom: 0px; } }

@media (max-width: 767px) { .poster-intro__bottom { flex-wrap: wrap; padding-right: 0; margin-bottom: 30px; } }

.poster-intro .tickets { max-width: 480px; }

@media (max-width: 1160px) { .poster-intro .tickets { max-width: 100%; padding-right: 40px; } }

.poster-intro .poster-module { max-width: 240px; margin-top: -120px; flex-shrink: 0; margin-left: 30px; }

@media (max-width: 767px) { .poster-intro .poster-module { margin-top: 0; margin-left: 0; max-width: 100%; width: 100%; } }

.poster-intro__content { flex-grow: 1; padding-top: 30px; }

.poster-intro__title { text-transform: uppercase; color: #22415e; font-family: "Acrom"; font-weight: 500; font-size: 30px; margin-bottom: 90px; }

@media (max-width: 1160px) { .poster-intro__title { margin-bottom: 30px; } }

@media (max-width: 767px) { .poster-intro__title { font-size: 24px; } }

.map-module { width: 100%; height: 450px; display: block; }

@media (max-width: 767px) { .map-module { height: 300px; } }
