@import "./menu-button";
@import "./calendar";

.color-red {
  color: $c10 !important;
}

.color-blue {
  color: #22415e !important;
}

.header {
  &__row {
    display: flex;
    align-items: center;
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .menu-button {
    display: none;
    margin-left: auto;
  }

  &__slogan {
    font-size: 13px;
    font-family: $notoc;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 0.52px;
    margin: 0 auto 0 50px;

    @media (max-width: $notebook-sm) {
      margin: 0 auto 0 35px;
    }

    @media (max-width: $tablet) {
      width: 170px;
    }
  }

  &__eye {
    width: 33px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;

    svg {
      height: 24px;
      width: 24px;
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    margin: -7.5px;
    margin-left: 40px;

    .btn {
      font-size: 12px;
      padding: 5px 12px;
      height: 32px;
    }

    &>* {
      margin: 7.5px;
    }
  }

  &__logo {
    img {
      display: block;
      max-width: 100%;
    }
  }

  @media (max-width: $tablet-sm) {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 50;
    background: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);

    .top-social,
    &__eye,
    &__buttons {
      display: none;
    }

    .menu-button {
      display: block;
    }

    &__row {
      padding-top: 0;
      padding-bottom: 0;
      height: 70px;
      align-items: center;
    }
  }

  @media (max-width: $mobile) {
    &__logo {
      max-width: 180px;
    }

    &__slogan {
      display: none;
    }
  }
}

@media (max-width: $tablet-sm) {
  body {
    padding-top: 70px;
  }
}

.top-social {
  display: flex;
  margin: -7.5px;
  margin-left: auto;

  li {
    margin: 7.5px;
  }

  img {
    height: 28px;
  }

  a {
    width: 33px;
    height: 32px;
    border: 1px solid $c8;
    opacity: 0.3;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s ease;

    &:hover {
      opacity: 1;
    }
  }
}

.nav {
  position: relative;
  z-index: 5;

  &--red {
    background: #bf1026;
  }

  ul {
    background: #bf1026;
    display: flex;
  }

  li {
    flex-grow: 1;
    text-align: center;

    &.active {
      a {
        background: #20405f;
      }
    }
  }

  a {
    color: #fff;
    height: 60px;
    line-height: 60px;
    display: block;
    font-size: 12px;
    font-family: $acrom;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 0.63px;
    color: #fffefe;
    text-decoration: none;

    &:hover {
      background: lighten(#bf1026, 5%);
    }
  }

  @media (max-width: $tablet-sm) {
    display: none;
  }
}

@mixin slider-prev-active() {
  background: #bf1026;
  border-color: #a2032d;
  z-index: 2;
  transition: all 0.5s $anim;

  .slider-preview__icon {
    opacity: 0.5;
  }

  &:before,
  &:after {
    opacity: 1;
    transition: all 0.5s $anim;
    width: calc(100% + 20px);
  }
}

.intro-posters {
  position: relative;

  &__group {
    display: flex;
    transform: translateX(-10px);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    position: absolute;
    top: 0;
    left: 0;

    &.active {
      opacity: 1;
      transform: none;
      visibility: visible;
    }

    @media (max-width: $notebook-xs) {
      position: relative;
      opacity: 1;
      visibility: visible;
      display: none;

      &.active {
        display: flex;
      }
    }
  }

  .intro-poster {
    border-right: solid 1px #d6dce1;
    height: 216px;

    &:last-child {
      border: none;
    }
  }
}

.intro-poster {
  background: rgba(#fff, 0.85);
  text-decoration: none;
  text-transform: uppercase;
  color: #22415e;
  font-family: $acrom;
  font-weight: 500;
  min-width: 190px;
  padding: 15px 25px;
  text-align: center;
  transition: all 0.1s ease;
  display: flex;
  flex-direction: column;

  &:hover {
    background: rgba(#fff, 1);
  }

  * {
    margin: 0 auto;
  }

  img {
    height: 43px;
    display: block;
    margin-bottom: 15px;
  }

  &__title {
    font-size: 15px;
    max-width: 130px;
    display: block;
    margin-bottom: 5px;
  }

  &__date {
    font-size: 11px;
    display: block;
    margin-bottom: 15px;
  }

  &__time {
    display: block;
    color: #bc072a;
    font-size: 17px;
    margin-bottom: 15px;
    margin-top: auto;
  }

  @media (max-width: $mobile-xs) {
    &__title {
      font-size: 12px;
    }
  }
}


.location-tooltip {
  position: absolute;
  background: #fff;
  color: #000;
  padding: 10px 15px;
  width: 230px;
  z-index: 9;

  &:after {
    content: "";
    position: absolute;
    top: 100%;
    left: 15px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 15px 12.5px 0 12.5px;
    border-color: $c10 transparent transparent transparent;
  }

  &__title {
    font-weight: 700;
    font-size: 16px;
    font-family: $acrom;
    text-transform: uppercase;
    margin-bottom: 5px;
  }

  &__desc {
    font-size: 11px;
  }

  @media (max-width: $tablet-sm) {
    position: relative;
    order: -1;
    left: 0 !important;
    top: 0 !important;
  }
}

.intro {
  position: relative;
  z-index: 1;
  padding-bottom: 60px;

  .intro-posters {
    position: absolute;
    bottom: 140px;
    height: 313px;
    padding-bottom: 75px;

    @media (max-width: $tablet) {
      position: relative;
      bottom: 0;
      padding-bottom: 0;
      height: auto;
      margin-top: -50%;
    }

    @media (max-width: $tablet-sm) {
      margin-top: -15%;
      width: 100%;

      .intro-poster {
        flex-grow: 1;
        background: rgba(#fff, 1);
      }
    }

    @media (max-width: $mobile) {
      overflow-x: auto;
      margin-top: 0;

      .intro-poster {
        min-width: 45%;
        padding-left: 15px;
        padding-right: 15px;
      }
    }
  }

  @media (max-width: $notebook) {
    padding-bottom: 140px;
  }

  @media (max-width: $tablet) {
    display: flex;
    flex-direction: column;
    padding-bottom: 0;
  }

  &__decor {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 90;
    width: 100%;
    height: 100%;

    @media (max-width: $notebook-xs) {
      display: none;
    }

    div {
      &:nth-child(1) {
        width: 9999px;
        height: 35%;
        display: block;
        position: absolute;
        right: calc(100% - 15px);
        bottom: 0;
        background: #b6c6d7;
      }

      &:nth-child(2) {
        width: 9999px;
        height: 65%;
        display: block;
        position: absolute;
        right: calc(100% - 15px + 160px);
        bottom: 35%;
        background: #e0e9f3;

        &:before {
          content: "";
          width: 160px;
          height: 100%;
          background: linear-gradient(to bottom,
              rgba(255, 255, 255, 0.2) 0%,
              rgba(246, 246, 246, 0.5) 47%,
              rgba(237, 237, 237, 0.8) 100%);
          display: block;
          position: absolute;
          bottom: 0;
          left: 100%;

          @media (max-width: $notebook) {
            width: 120px;
            right: calc(100% - 15px + 120px);
          }
        }

        &:after {
          content: "";
          width: 160px;
          height: 50%;
          background: #e5ebf1;
          display: block;
          position: absolute;
          bottom: 0;
          left: 100%;
        }
      }
    }
  }

  .intro-cards {
    position: absolute;
    z-index: 10;
    right: 15px;
    bottom: 140px;

    @media (max-width: $tablet) {
      position: relative;
      bottom: inherit;
      right: 0;
      margin-left: 1px;
      width: calc(30% - 1px);
    }

    @media (max-width: $mobile) {
      width: 100%;

      .intro-card {
        width: 100%;
      }
    }
  }

  .container {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 4;
    display: flex;
    align-items: flex-end;

    @media (max-width: $tablet) {
      position: relative;
      transform: none;
      left: inherit;
      order: 2;
      padding: 0;
      width: 100%;
      margin: 0;
      max-width: 100%;
      flex-wrap: wrap;

      .intro-card {
        height: 140px;
        margin-bottom: 1px;
        width: auto;
      }
    }
  }

  &__image {
    height: 100%;
    margin: 0 auto;
    width: 85vw;
    max-width: 1543px;
    position: relative;

    .container {
      pointer-events: none;
      height: calc(100% + 60px);
      bottom: -60px !important;
    }

    @media (max-width: $notebook) {
      .container {
        height: calc(100% + 140px);
        position: absolute;
        bottom: -140px !important;
      }
    }

    @media (max-width: $notebook-sm) {
      width: calc(100% - 120px);
    }

    @media (max-width: $notebook-xs) {
      width: calc(100%);
    }

    @media (max-width: $tablet-sm) {
      display: flex;
      flex-direction: column;

      .location-tooltip {
        width: 100%;
        padding-top: 30px;
        padding-bottom: 30px;
        text-align: center;

        &:after {
          left: 50%;
          transform: translateX(-50%);
        }
      }

      svg {
        top: initial !important;
        bottom: 0;
      }

      .container {
        position: relative;
        order: -1;
        bottom: 0 !important;
      }
    }

    @media (max-width: $mobile) {
      .container {
        height: 100%;
        bottom: 0 !important;
      }
    }

    &:before {
      content: "";
      width: 50%;
      height: 80%;
      border-top: solid 60px rgb(191, 16, 38);
      border-right: solid 60px rgb(191, 16, 38);
      display: block;
      position: absolute;
      top: -60px;
      right: -60px;
      z-index: 1;
      pointer-events: none;

      @media (max-width: $notebook-xs) {
        display: none;
      }
    }

    &::after {
      content: "";
      width: 50%;
      height: 100%;
      background: #22415e;
      display: block;
      position: absolute;
      bottom: -60px;
      right: -60px;
      z-index: -1;
      pointer-events: none;

      @media (max-width: $notebook) {
        bottom: -140px;
      }

      @media (max-width: $notebook-xs) {
        display: none;
      }
    }

    img,
    svg {
      display: block;
      margin: 0 auto;
      max-width: 100%;
    }

    svg {
      position: absolute;
      top: 0;
      left: 0;

      .object-point {
        opacity: 0;
        transition: all 0.2s ease;
        cursor: pointer;
        z-index: 3;

        &.active,
        &:hover {
          opacity: 1;
        }
      }
    }
  }

  &__slider-previews {
    margin-top: auto;
    display: flex;
    position: relative;
    z-index: 5;
    width: 100%;

    @media (max-width: $tablet) {
      flex-wrap: wrap;
      width: 70%;
    }

    @media (max-width: $mobile) {
      width: 100%;
    }

    .slider-preview {
      width: 25%;
      height: 140px;

      @media (max-width: $tablet) {
        width: calc(50% - 1px);
        margin: 0 0.5px 1px;

        &:before,
        &:after {
          display: none;
        }
      }

      @media (max-width: $mobile) {
        height: auto;
      }
    }
  }

  @media (max-width: $mobile-xs) {}
}

.intro-card {
  background: #22415e;
  color: #fff;
  padding: 20px 30px;
  width: 280px;

  &--red {
    background: url("../img/bg-intro-card.png") center no-repeat;
    background-size: cover;
  }

  &__desc {
    text-transform: uppercase;
    font-size: 15px;
    letter-spacing: 0.79px;
    font-family: $notoc;
    margin: 5px 0;
  }

  &__count {
    font-size: 52px;
    line-height: 1;
    font-weight: 700;
    font-family: $acrom;
    margin: 5px 0;

    small {
      display: block;
      font-size: 70%;
    }
  }

  // @media (max-width: $notebook) {
  //   &__count {
  //     font-size: 38px;
  //   }
  // }

  @media (max-width: $tablet) {
    padding: 15px 20px;

    &__count {
      font-size: 33px;
    }

    &__desc {
      font-size: 16px;
    }
  }

  @media (max-width: $tablet-sm) {
    padding: 20px 30px;

    &__desc {
      font-size: 14px;
    }
  }
}

.slider-preview {
  padding: 45px 30px;
  display: flex !important;
  align-items: center;
  text-decoration: none;
  flex-grow: 1;
  color: #fff;
  background: #20405f;
  border-left: solid 20px transparent;
  position: relative;
  //transition: all .3s $anim;
  $parent: &;

  &:before {
    content: "";
    width: 0;
    height: 20px;
    display: block;
    background: #a2032d;
    position: absolute;
    bottom: 100%;
    left: -20px;
    opacity: 0;
    transition: all 0.5s $anim;
  }

  &:after {
    content: "";
    width: 0;
    height: 20px;
    display: block;
    background: #a2032d;
    position: absolute;
    top: 100%;
    left: -20px;
    opacity: 0;
    transition: all 0.5s $anim;
  }

  &__count {
    display: block;
    font-family: $acrom;
    font-size: 37px;
    line-height: 1;
    margin-bottom: 5px;
    font-weight: 400;
    text-transform: uppercase;
  }

  &__name {
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 0.26px;
    font-weight: 500;
    color: rgba(#fff, 0.5);
  }

  &__icon {
    margin-right: 20px;
    opacity: 0.3;

    img {
      height: 46px;
    }
  }

  &.active {
    @include slider-prev-active();
  }

  @media (max-width: $notebook) {
    padding: 40px 30px;
  }

  @media (max-width: $tablet-sm) {
    padding: 30px 15px !important;
  }

  @media (max-width: $tablet) {
    border-left: solid 10px transparent;

    &:hover,
    &.active {
      border-color: transparent;
    }

    &:before,
    &:after {
      content: "";
      width: calc(100% + 10px) !important;
      height: 10px;
      left: -10px;
    }

    &:before {
      top: -10px;
    }

    &:after {
      bottom: -10px;
    }
  }

  @media (max-width: $mobile) {
    flex-wrap: wrap;
    padding: 30px 20px;

    &__icon {
      margin: 0 0 15px 0;
    }

    &__content {
      width: 100%;
    }
  }
}

.footer {
  background-color: #0f304e;
  color: #fff;

  .contacts-block {
    max-width: 340px;
  }

  &__top {
    padding-top: 60px;
    padding-bottom: 60px;
    display: flex;
  }

  &__col {
    width: 50%;
  }

  &__links {
    font-size: 13px;
    margin-bottom: -10px;

    a {
      color: rgba(#fff, 0.5);
      text-decoration: underline;
      margin: 0 35px 10px 0;
      white-space: nowrap;
      display: inline-block;

      &:hover {
        text-decoration: none;
      }
    }
  }

  &__nav {
    width: 100%;
    margin-bottom: 60px;

    a {
      color: #fffefe;
      text-transform: uppercase;
      letter-spacing: 0.63px;
      font-size: 12px;
      font-weight: 500;
      font-family: $acrom;
      text-decoration: none;

      &:hover {
        text-decoration: underline;
      }
    }

    li {
      margin: 0 30px 20px 0;

      &.active {
        a {
          color: rgba(#fff, 0.5);
        }
      }
    }

    ul {
      display: flex;
      flex-wrap: wrap;
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    border-top: 1px solid #bc072a;
    padding-top: 35px;
    padding-bottom: 35px;
  }

  &__copyright {
    color: rgba(#fff, 0.5);
  }

  @include media-breakpoint-down(md) {
    &__top {
      flex-wrap: wrap;
      padding-top: 30px;
      padding-bottom: 30px;
    }

    &__nav {
      margin-bottom: 30px;
      display: none;
    }

    .contacts-block {
      width: 100%;
      //display: none;
    }

    &__col {
      width: 100%;
      margin-bottom: 30px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    &__links {
      margin-top: 15px;
      order: 2;

      a {
        margin-right: 10px;
      }
    }

    &__bottom {
      flex-wrap: wrap;
    }

    &__copyright {
      margin-bottom: 15px;
    }
  }
}

.contacts-block {
  font-size: 16px;
  margin-bottom: 40px;

  &:last-child {
    margin-bottom: 0;
  }

  &__name {
    color: rgba(#fff, 0.5);
    text-transform: uppercase;
    letter-spacing: 0.69px;
    font-size: 12px;
    font-family: $acrom;
    margin-bottom: 10px;
  }
}

.social-module {
  display: flex;
  align-items: center;
  margin: -10px;

  svg {
    height: 28px;
    width: 33px;
    display: block;
    fill: #bc072a;
  }

  ul {
    display: flex;
  }

  li {
    margin: 10px;
  }

  a {
    display: block;

    &:hover {
      svg {
        fill: #fff;
      }
    }
  }
}

.section {
  margin: 80px 0;
}

.module-title {
  $parent: &;
  color: #3f3f3f;

  &__title {
    font-size: 37px;
    font-weight: 400;
    line-height: 1.3;
    text-transform: uppercase;
    letter-spacing: -0.85px;
    font-family: $acrom;

    @media (max-width: $mobile) {
      font-size: 24px !important;
    }
  }

  &--line {
    text-align: center;
    position: relative;
    display: flex;
    align-items: center;
    overflow: hidden;
    margin-bottom: 50px;

    #{$parent}__title {
      margin: 0 30px;
      flex-grow: 1;
      flex-shrink: 0;

      @media (max-width: $mobile) {
        max-width: 70%;
        margin: 0 10px;
      }
    }

    &:before {
      content: "";
      width: 9999px;
      height: 1px;
      background: #3f3f3f;
      display: block;
    }

    &:after {
      content: "";
      width: 9999px;
      height: 1px;
      background: #3f3f3f;
      display: block;
    }
  }

  &--right {
    text-align: right;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  min-height: 100vh;
  overflow: hidden;

  &>* {
    width: 100%;
  }
}

.page-wrap {
  margin-bottom: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-grow: 1;
  $parent: &;
}

.mobile-menu {
  display: none;
  z-index: 15;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;

  a {
    text-decoration: none;
  }

  &__nav {
    overflow: auto;
    height: calc(100% - 70px);

    ul {
      margin-bottom: 30px;
    }

    ul:not(.top-social) {
      li {
        a {
          display: block;
          padding: 15px;
          font-size: 18px;
          color: #000;
          border-bottom: 1px solid rgb(221, 228, 239);
        }

        &.active {
          a {
            color: $c10;
          }
        }
      }
    }
  }

  &__buttons {
    background: rgb(221, 228, 239);

    .btn {
      min-width: 40px;
    }

    .container {
      display: flex;
      height: 70px;
      align-items: center;

      &>* {
        margin: 5px;
      }
    }
  }

  @media (max-width: $tablet-sm) {
    &.open {
      display: block;
    }
  }
}

@mixin bButtons() {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
  margin-left: -15px;
  margin-right: -15px;
  margin-bottom: -15px;

  &>* {
    margin: 15px;
  }
}

@mixin itemDate() {
  color: #3f3f3f;
  font-size: 45px;
  line-height: 1;
  font-family: $acrom;
  font-weight: 100;
  display: block;
  margin-bottom: 30px;

  @media (max-width: $tablet-sm) {
    font-size: 30px;
  }
}

@mixin itemTitle() {
  font-size: 18px;
  font-weight: 700;
  display: block;
  margin-bottom: 30px;
}

.video {
  display: block;
  background: #fff;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.15);
  position: relative;
  overflow: hidden;
  transition: all 0.3s $anim;

  &:before {
    content: "";
    width: 18%;
    height: 105px;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    background: url("../img/icons/play.svg") center no-repeat;
    background-size: contain;
    opacity: 0.4;
    transition: all 0.3s $anim;
    z-index: 2;
  }

  img {
    display: block;
    width: 100%;
    transition: all 1s $anim;
  }

  &:hover {
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.15);

    img {
      transform: scale(1.05);
    }

    &:before {
      opacity: 1;
