    position: relative;

    &--large {
      width: 100% !important;
      left: 50% !important;
      position: relative !important;
      right: auto;
      margin-right: 0;
      margin-top: 0;
    }

    &--read-more {
      &:before {
        content: "";
        width: 100%;
        height: 100%;
        background: rgba(#3f3f3f, 0.8);
        position: absolute;
