      &:before {
        content: "";
        width: 9999px;
        height: 690px;
        background: rgb(32, 64, 95);
        border-right: solid 65px rgb(188, 7, 42);
        border-bottom: solid 65px rgb(188, 7, 42);
        display: block;
        position: absolute;
        right: 100%;
        top: -80px;
        z-index: 2;
        margin: 0 155px 0 0;
        transition: all 1s $anim;
        transform-origin: top;
        transform: scaleY(0);
        z-index: 3;
      }

      &:after {
        content: "";
        width: 430px;
        height: 0;
        border-right: solid 65px rgb(188, 7, 42);
        border-top: solid 65px rgb(188, 7, 42);
        display: block;
        position: absolute;
        right: 100%;
        top: -80px;
        z-index: 2;
        margin: 0 0 0 0;
        transition: all 1s $anim 1s;
        transform-origin: left;
        transform: scaleX(0) translateX(50%);
      }
    }
  }

  .module-title {
    margin-bottom: 80px;
    color: #22415e;

