    display: flex;
    flex-wrap: wrap;
    margin: -20px;
  }

  &__col {
    width: 25%;
    padding: 20px;
  }

  &__buttons {
    @include bButtons();
  }

  &.show {
    &#{$parent}--decor {
      .container {
        &:before {
          transform: none;
        }

        &:after {
          transform: scaleX(1) translateX(50%);
        }
      }
    }
  }

  @media (max-width: $tablet-sm) {
    &__col {
      width: 50%;
    }
  }

  @media (max-width: $mobile) {
    padding-top: 40px;

    .module-title {
      text-align: center;
    }

    &--decor {
      .container {

        &:after,
        &:before {
          display: none;
        }
      }
    }

    &__row {
      margin: 0;
    }

    .module-title {
      margin-bottom: 15px;
    }

    &__buttons {
      margin-top: 30px;
    }

    &__col {
      width: 100%;
      padding: 10px;
    }
  }
