      height: 230px;
      background: #e0e9f3;
      display: block;
      position: absolute;
      top: 0;
      right: 100%;
      margin-right: 160px;
      z-index: 2;
      transition: all 1.5s $anim;
      transform: scale(0);
      transform-origin: top right;
    }

    &:before {
      content: "";
      width: 99999px;
      height: 100%;
      background: #20405f;
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 100%;
      margin-right: -15px;
    }
  }

  &__img {
    position: absolute;
    bottom: 0;
    right: -8%;
