      max-height: 75px;
    }
  }

  @media (min-width: $tablet) {
    &:hover {
      background: #fff;
      padding-bottom: 60px;

      #{$parent}__desc {
        color: #000;
      }

      #{$parent}__title {
        color: $c10;
      }

      #{$parent}__btn {
        opacity: 1;
      }
    }
  }

  @media (max-width: $tablet-sm) {
    &__title {
      font-size: 22px;
      max-height: 49px;
    }

    min-height: 290px;
  }

  @media (max-width: $mobile) {
    padding: 20px;
    min-height: 220px;

    &__title {
      font-size: 18px;
      max-height: 55px;
    }

    &__desc {
      margin-bottom: 0;
    }
