.intro-banner {
  height: 255px;
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;

  @media (max-width: $mobile) {
    height: 180px;
  }
}

.annotation {
  padding: 50px 0 0 50px;
  margin-bottom: 0 !important;
  position: relative;

  &__top {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;

    .page-title {
      margin-bottom: 0;
    }
  }

  &--lighten {
    background: #e0e9f3;
    padding-bottom: 50px;

    &:after {
      content: "";
      width: 9999px;
      height: 100%;
      background: inherit;
      position: absolute;
      left: 100%;
      top: 0;
      bottom: 0;
    }

    // bg color отличается
    .breadcrumbs {
      &:after {
        background: linear-gradient(to right,
            rgba(#e0e9f3, 0) 0%,
            #e0e9f3 100%);
      }
    }

    @media (max-width: $tablet-sm) {
      &:before {
        content: "";
        width: 9999px;
        height: 100%;
        background: inherit;
        position: absolute;
        right: 100%;
        top: 0;
        bottom: 0;
      }
    }

    @media (max-width: $mobile) {
      padding-bottom: 30px;
    }
  }

  @media (max-width: $tablet) {
    padding-top: 30px;
    display: flex;
    flex-direction: column;

    &__top {
      order: 3;
      margin-top: 14px;
      margin-bottom: 0;

      .page-title {
        margin-bottom: 15px;
      }
    }
  }
}

.filter {
  display: flex;
  flex-wrap: wrap;
  margin: -5px;

  &>* {
    margin: 5px;
  }

  select {
    height: 40px;
    border: solid 1px #153260;
    text-transform: uppercase;
    min-width: 100px;
    color: #153260;
    font-weight: 500;
    padding-left: 10px;
    padding-right: 10px;
    background: transparent;
    font-family: $acrom;
  }

  label {
    display: flex;

    span {
      text-transform: uppercase;
      font-size: 15px;
      font-weight: 500;
      font-family: $acrom;
      letter-spacing: -0.3px;
      color: #22415e;
      padding: 10px 20px;
      border: solid 1px transparent;
      cursor: pointer;

      &:hover {
        color: $c10;
      }

      @media (max-width: $mobile) {
        padding: 5px 10px;
        font-size: 13px;
      }
    }

    input {
      display: none;

      &:checked+span {
        color: $c10;
        border-color: $c10;
      }
    }
  }
}

.news-block {
  display: flex;
  background: #cbd8e5;
  text-decoration: none;
  color: #000;
  $parent: &;

  &__content {
    padding: 30px 15px 30px 25px;
    flex-grow: 1;
    min-width: 50%;
  }

  &__title {
    font-weight: 700;
    font-size: 13px;
    margin-bottom: 8px;
    display: block;
    font-family: $noto;
  }

  &__date {
    color: rgba(#3f3f3f, 0.5);
    margin-bottom: 8px;
    font-size: 10px;
    font-family: $noto;
  }

  &__desc {
    color: rgba(#3f3f3f, 0.5);
    font-size: 11px;
    line-height: 1.5;
    display: block;
  }

  &__image {
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 50%;

    img {
      display: block;
      height: 100%;
      flex-shrink: 0;
      transition: all 0.2s $anim;
    }
  }

  &:hover {
    #{$parent}__image {
      img {
        transform: scale(1.1);
      }
    }

    #{$parent}__title {
      color: $c10;
      text-decoration: underline;
    }
  }

  @media (max-width: $tablet) {
    &__image {
      min-width: auto;
      flex-shrink: 0;
    }

    &__title {
      font-size: 16px;
    }

    &__desc,
    &__date {
      font-size: 14px;
    }
  }

  @media (max-width: $mobile) {
    // flex-wrap: wrap;
    align-items: center;

    &__title {
      font-size: 14px;
    }

    &__image {
      max-width: 45%;
      align-items: flex-start;

      img {
        height: auto;
        max-width: 100%;
      }
    }

    &__desc {
      display: none;
    }

    &__content {}
  }

  @media (max-width: $mobile-xs) {
    &__image {
      display: none;
    }

    &__content {
      min-width: 100%;
    }

    &__desc {
      display: block;
    }
  }
}

.news-module {
  display: flex;
  background: #dfe7f0;
  text-decoration: none;
  color: #000;
  $parent: &;

  &__content {
    padding: 30px 50px 30px 35px;
    min-width: 60%;
    width: calc(100% - 260px);
  }

  &__title {
    font-weight: 700;
    font-size: 20px;
    color: #22415e;
    text-decoration: underline;
    margin-bottom: 10px;
    display: block;
    font-family: $noto;
  }

  &__date {
    color: rgba(#22415e, 0.6);
    margin-bottom: 10px;
    font-size: 12px;
    font-family: $noto;
  }

  &__desc {
    color: rgba(#000, 1);
    font-size: 15px;
    text-align: justify;
    line-height: 1.5;
    display: block;
  }

  &__image {
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 260px;
    max-width: 40%;

    img {
      display: block;
      width: 100%;
      transition: all 0.2s $anim;
    }
  }

  &:hover {
    #{$parent}__image {
      img {
        transform: scale(1.1);
      }
    }

    #{$parent}__title {
      color: $c10;
      text-decoration: underline;
    }
  }

  @media (max-width: $mobile) {
    // flex-wrap: wrap;
    align-items: center;

    &__title {
      font-size: 14px;
    }

    &__image {
      margin-bottom: auto;

      img {
        height: auto;
        max-width: 100%;
      }
    }

    &__date {
      margin-bottom: 0;
    }

    &__desc {
      display: none;
    }

    &__content {
      padding: 15px;
    }
  }
}

.news-intro {
  display: flex;
  margin-bottom: 60px !important;

  &__image {
    width: 50%;

    img {
      display: block;
      max-width: 100%;
      margin: 0 auto;
    }

    @media (max-width: $tablet) {
      width: 40%;
    }
  }

  //align-items: center;
  &__content {
    width: 50%;
    padding: 60px 60px 0;
    margin-top: 30px;
    position: relative;

    @media (max-width: $tablet) {
      padding: 40px 40px 0;
      width: 60%;
    }

    &:after {
      content: "";
      height: calc(100% + 30px);
      width: 10px;
      background: #bf1026;
      display: block;
      position: absolute;
      top: 0;
      left: 100%;
    }

    &:before {
      content: "";
      width: calc(100% + 30px);
      height: 10px;
      background: #bf1026;
      display: block;
      position: absolute;
      top: 0;
      right: 0;
    }
  }

  &__title {
    margin-bottom: 10px;
    font-size: 23px;
    color: #000;
  }

  &__date {
    margin-bottom: 15px;
    opacity: 0.6;
    color: #22415e;
    font-size: 14px;
  }

  &__desc {
    font-size: 16px;
    line-height: 1.5;
    font-weight: 700;
    text-align: justify;
    color: #000;

    @media (max-width: $mobile) {
      text-align: left;
    }
  }

  @media (max-width: $mobile) {
    flex-wrap: wrap;

    &__content {
      width: 100%;
      margin-top: -10px;
      padding: 30px 30px 0 15px;
    }

    &__image {
      width: 100%;
    }
  }
}

.page {
  background: #f3f6fa;
  $parent: &;

  &__row {
    display: flex;
  }

  &__sidebar {
    flex-shrink: 0;
    width: 240px;
    background: #ccd9e6;
    position: relative;
    padding-right: 20px;

    .inner-wrapper-sticky {
      padding-top: 50px;
      padding-bottom: 80px;

      &>* {
        margin-bottom: 30px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      .sidebar-navigation {
        margin-bottom: 50px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    &:before {
      content: "";
      width: 9999px;
      height: 100%;
      background: inherit;
      position: absolute;
      right: 100%;
      top: 0;
    }
  }

  &__inner {
    padding: 50px 0 80px 50px;

    &>* {
      margin-bottom: 30px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    .buttons {
      margin-top: 30px;

      .btn {
        font-size: 11px;
        font-weight: 700;
      }

      &:first-child {
        margin-top: 0;
      }
    }

    .section-title {
      margin-bottom: 50px;
      margin-top: 50px;

      &:first-child {
        margin-top: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    .news-block,
    .poster-item {
      margin-bottom: 30px;
    }

    .poster-item {
      font-size: 15px;

      &__top {
        height: 100px;
      }

      .btn {
        font-size: 10px;
        padding-left: 10px;
        padding-right: 10px;
        height: 30px;
      }

      &__title {
        font-size: 24px;
        max-height: 55px;
      }

      &__date {
        font-size: 14px;
      }
    }

    @media (max-width: $tablet) and (min-width: $mobile) {
      .poster-list {
        &>*:nth-last-child(2) {
          display: none;
        }
      }
    }
  }

  &__content {
    width: calc(100% - 240px);

    .sidebar-navigation {
      margin: 0;
      border-bottom: 1px solid rgb(213, 219, 227);
      padding-bottom: 30px;
    }
  }

  &--full {
    #{$parent}__content {
      width: 100%;
    }

    .annotation {
      padding-left: 0;
    }

    #{$parent}__inner {
      padding-left: 0;
    }
  }

  @media (max-width: $tablet-sm) {
    &__row {
      flex-wrap: wrap;
    }

    &__sidebar {
      width: 100%;

      &::after {
        content: "";
        width: 9999px;
        height: 100%;
        background: inherit;
        position: absolute;
        left: 100%;
        top: 0;
      }

      .inner-wrapper-sticky {
        padding-top: 0;
        padding-bottom: 0;

        .sidebar-navigation {
          padding-top: 25px;
          padding-bottom: 15px;
        }

        &>*:not(.sidebar-navigation) {
          display: none;
        }
      }
    }

    &__inner,
    .annotation {
      padding-left: 0;
    }

    &__content {
      width: 100%;
    }
  }

  @media (max-width: $mobile) {
    &__inner {
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }
}

.sidebar-nav-toggle {
  padding: 15px 0;
  cursor: pointer;
  font-family: $acrom;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 1px;
  background: #bf1026;
  position: relative;
  color: #fff;
  z-index: 10;

  .container {
    position: relative;

    &:after {
      content: "";
      width: 10px;
      height: 10px;
      border-bottom: 1px solid #fff;
      border-right: solid 1px #fff;
      position: absolute;
      top: 50%;
      right: 25px;
      transform-origin: center;
      margin-top: -5px;
      transform: translateY(-50%) rotate(45deg);
    }
  }

  &.active {
    .container {
      &:after {
        transform: translateY(-50%) rotate(225deg);
        margin-top: 0;
      }
    }
  }
}

.breadcrumbs {
  margin-bottom: 40px;
  margin-left: -5px;
  margin-right: -5px;
  font-family: $acrom;
  color: #22415e;
  text-transform: uppercase;
  font-size: 11px;
  white-space: nowrap;
  width: 100%;
  position: relative;
  $parent: &;
  height: 13px;

  &:after {
    content: "";
    width: 50px;
    height: 100%;
    background: #000;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 5;
    background: linear-gradient(to right,
        rgba(#f3f6fa, 0) 0%,
        rgba(#f3f6fa, 1) 100%);
  }

  &__inner {
    overflow: auto;
    position: relative;

    // off visible scroll
    overflow: -moz-scrollbars-none;
    -ms-overflow-style: none;

    &::-webkit-scrollbar {
      width: 0;
    }

    //-
  }

  a {
    color: #22415e;
    text-decoration: underline;

    &:hover {
      color: $c10;
    }
  }

  * {
    margin: 0 5px;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.page-title {
  * {
    font-size: 45px;
    font-family: $acrom;
    color: #22415e;
    text-transform: uppercase;
    letter-spacing: -0.9px;
    font-weight: 500;
    line-height: 1.3;
  }

  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (max-width: $mobile) {
    * {
      font-size: 24px;
    }
  }
}

.section-title {
  display: flex;
  align-items: center;

  &:after {
    content: "";
    width: 100%;
    height: 2px;
    display: block;
    background: #c9d7e6;
    margin-left: 20px;
  }

  * {
    font-size: 32px;
    font-weight: 500;
    font-family: $acrom;
    text-transform: uppercase;
    color: #4a6078;
    line-height: 1.3;
    flex-shrink: 0;

    @media (max-width: $mobile) {
      font-size: 24px;
    }

    @media (max-width: $mobile-xs) {
      font-size: 22px;
    }
  }
}

.sidebar-navigation {
  margin-top: -15px;

  a {
    text-transform: uppercase;
    color: #22415e;
    font-family: $acrom;
    font-weight: 700;
    letter-spacing: 0.77px;
    text-decoration: none;
    font-size: 14px;
    padding-bottom: 15px;
    padding-top: 15px;
    padding-right: 15px;
    display: block;

    &:hover {
      color: #000;
    }
  }

  li {
    position: relative;
    padding-left: 40px;

    &.active {
      &>a {
        color: $c10;
      }
    }

    &:before {
      content: "";
      width: 22px;
      height: 2px;
      background: $c10;
      display: block;
      position: absolute;
      top: 22px;
      left: 0;
      transition: all 0.2s $anim;
    }

    &:last-child {
      margin-bottom: 0;
    }

    ul {
      margin-left: -40px;

      a {
        font-weight: 400;
      }

      li {
        &:before {
          height: 1px;
          background: #698aaa;
        }
      }
    }

    &:hover {
      &:before {
        transform: translateX(-5px);
      }
    }
  }
}

.sidebar-banner {
  display: block;

  img {
    display: block;
    max-width: 100%;
  }
}

.photo-gallery {
  img {
    display: block;
    max-width: 100%;
  }

  display: flex;
  flex-wrap: wrap;

  a:not(.photo) {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    min-height: 100%;
    position: relative;

    &:before {
      content: "";
      width: 100%;
      height: 100%;
      background: rgba(#3f3f3f, 0.8);
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 2;
      opacity: 0;
      transition: all 0.2s $anim;
    }

    &:hover {

      &:before,
      .photo-desc {
        opacity: 1;
      }
    }
  }

  .photo-desc {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    color: #fff;
    z-index: 3;
    border: solid 1px #fff;
    font-family: $acrom;
    max-width: 180px;
    max-height: 150px;
    overflow: hidden;
    font-size: 18px;
    text-align: center;
    padding: 15px;
    font-weight: 500;
    opacity: 0;
    transition: all 0.2s $anim;
  }

  li {
    width: 33.333%;
    padding: 1px;

    &.photo-gallery__large {
      width: 100%;
    }
  }
}

.quote {
  background: #fff;
  padding: 30px 60px 40px 30px;
  position: relative;
  display: flex;
  font-family: $noto;

  .typography {
    font-size: 18px;
  }

  &:before {
    content: "";
    display: block;
    width: 109px;
    height: 109px;
    flex-shrink: 0;
    background: url("../img/quote.png") center no-repeat;
    background-size: contain;
  }

  &__content {
    padding-left: 75px;
    display: block;
  }

  &__text {
    position: relative;

    &:before {
      content: "";
      width: 40px;
      height: 3px;
      background: $c10;
      display: block;
      position: absolute;
      top: 0.75em;
      right: 100%;
      margin-right: 20px;
    }
  }

  &__info {
    margin-bottom: 30px;
    color: #22415e;
    min-height: 109px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__name {
    font-size: 13px;
    font-style: italic;
    font-weight: 300;
    margin-bottom: 5px;
  }

  &__username {
    font-size: 19px;
    font-weight: 700;
    margin-bottom: 5px;
  }

  &__post {
    font-size: 19px;
  }

  @media (max-width: $mobile) {
    flex-wrap: wrap;

    &:before {
      width: 50px;
      height: 50px;
    }

    .typography {
      font-size: 14px;
    }

    &__content {
      padding-left: 0px;
    }

    &__text {
      &:before {
        display: none;
      }
    }
  }
}

.documents {
  &__title {
    font-size: 22px;
    color: #0010ac;
    font-weight: 500;
    margin-bottom: 15px;
  }

  li {
    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.document {
  display: flex;
  border: solid 3px #fff;
  padding: 15px 25px;
  position: relative;
  font-size: 17px;
  font-family: $noto;
  color: #000;
  text-decoration: none;
  font-weight: 500;

  &:before {
    content: "";
    width: 19px;
    height: 24px;
    display: block;
    background: url("../img/icons/doc.svg") center no-repeat;
    background-size: contain !important;
    margin-right: 20px;
    flex-shrink: 0;
  }

  &__name {
    padding-right: 10px;
  }

  &__size {
    font-weight: 300;
    color: #000000;
    margin-left: auto;
    flex-shrink: 0;
    white-space: nowrap;
  }

  &:hover {
    background: #fff;
  }
}

.block-slider {
  width: 100%;
  display: block;
  overflow: hidden;
  $parent: &;

  img {
    display: block;
    max-width: 100%;
  }

  * {
    vertical-align: top;
  }

  .slick-arrow {
    font-size: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;
    background-color: rgba(#e0e9f3, 0.7);
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s $anim;

    &:after {
      content: "";
      width: 18px;
      height: 28px;
      display: block;
      background: url("../img/icons/arrow.svg") center no-repeat;
      background-size: contain;
    }

    &:hover {
      background-color: rgba(#e0e9f3, 1);
    }

    &.slick-prev {
      left: 0;
      transform: rotate(-180deg) translateY(50%);
    }

    &.slick-next {
      right: 0;
      transform: translateY(-50%);
    }

    &.slick-disabled {
      opacity: 0;
    }
  }

  a {
    display: block;
    position: relative;
    text-decoration: none;
  }

  &__date {
    opacity: 0.5;
    color: #ffffff;
  }
}

.photo {
  position: relative;
  text-decoration: none;
  display: block;

  &__substrate {
    background: rgba(#22415e, 0.8);
    padding: 20px 30px;
    color: #fff;
    font-size: 14px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    &>* {
      margin: 10px;
    }

    @media (max-width: $mobile) {
      position: relative;
      padding: 15px;
    }
  }
}

.doc {
  padding: 20px;
  font-size: 14px;
  border: solid 3px #cad7e5;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 2px solid #cad7e5;
    padding-bottom: 20px;
  }

  &__content {
    padding-top: 20px;
  }

  &__date {
    color: #22415e;
    font-size: 14px;
    font-weight: 300;
    font-family: $noto;
    padding-left: 15px;
    flex-shrink: 0;
  }

  &__title {
    font-size: 17px;
    font-weight: 500;
    text-decoration: none;
    color: #000;
    font-family: $noto;
    display: flex;

    &:before {
      content: "";
      width: 19px;
      height: 24px;
      display: block;
      background: url("../img/icons/doc.svg") center no-repeat;
      background-size: contain !important;
      margin-right: 20px;
      flex-shrink: 0;
    }

    &:hover {
      color: $c10;
    }
  }

  .btn {
    margin-top: 20px;
    font-size: 11px;
    height: 30px;
  }

  @media (max-width: $mobile) {
    &__top {
      flex-wrap: wrap;
    }

    &__date {
      padding-left: 0;
      width: 100%;
      margin-top: 15px;
    }
  }
}

.pagination {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  $parent: &;

  a {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    margin: 0 5px;
    text-transform: uppercase;
    letter-spacing: 0.64px;
    font-weight: 500;
    font-family: $noto;
    cursor: pointer;
  }

  &__values {
    display: flex;
    margin-left: -5px;
    margin-right: -5px;

    a {
      background: #c9d7e6;
      color: #000000;
      font-size: 15px;
      text-decoration: none;

      &:hover {
        background: darken(#c9d7e6, 10%);
      }

      &.active {
        background: #20405f;
        color: #fff;
      }
    }
  }

  &__next {
    svg {
      transform: rotate(180deg);
    }
  }

  &__arrows {
    display: flex;
    margin-left: -5px;
    margin-right: -10px;

    svg {
      width: 32px;
      height: 21px;
      display: block;
      fill: #cad7e5;
    }

    a {
      width: auto;
      padding-left: 10px;
      padding-right: 10px;

      &:hover {
        svg {
          fill: $c10;
        }
      }
    }

    @media (max-width: $mobile) {
      margin: 15px 0 0 0;
      justify-content: center;
      width: 100%;
    }
  }

  &--months {
    #{$parent}__values {
      a {
        padding-left: 25px;
        padding-right: 25px;
        width: auto;
      }

      @media (max-width: $mobile) {
        width: 100%;
        margin: 0;

        a {
          width: 50%;
        }
      }
    }
  }

  @media (max-width: $mobile-xs) {
    justify-content: center;

    &__arrows,
    &__numbers {
      width: 100%;
      justify-content: center;
      margin-bottom: 15px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.album {
  display: flex;
  flex-direction: column;
  position: relative;
  color: #fff;
  text-decoration: none;
  $parent: &;

  &__name {
    font-size: 16px;
    font-weight: 500;
    margin-right: 30px;
  }

  .btn {
    color: #fff;
    flex-shrink: 0;
  }

  img {
    display: block;
    max-width: 100%;
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 25px 40px;
    position: absolute;
    z-index: 2;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(#20405f, 0.8);
  }

  &--intro {
    #{$parent}__bottom {
      text-align: center;
      justify-content: center;

      @media (max-width: $mobile) {
        padding: 15px;
      }
    }

    #{$parent}__name {
      font-size: 20px;
      margin-right: 0;

      @media (max-width: $mobile) {
        font-size: 16px;
      }
    }
  }

  @media (max-width: $mobile) {
    &__bottom {
      position: relative;
      order: 3;
      flex-wrap: wrap;
      background: rgba(#20405f, 1);
      padding: 25px;
    }

    .btn {
      margin-top: 20px;
    }
  }
}

.gallery-list {
  display: flex;
  flex-wrap: wrap;
  margin: -2px;

  li {
    width: 33.333333%;
    padding: 2px;
  }

  img {
    display: block;
    max-width: 100%;
    transition: all 0.2s ease;
  }

  a {
    display: block;
    overflow: hidden;

    &:hover {
      img {
        transform: scale(1.1);
      }
    }
  }
}

.search-module {
  form {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
  }

  .field {
    flex-grow: 1;
  }

  .btn {
    height: 60px;
    min-width: 120px;
  }

  input {
    height: 60px;
    border: solid 1px #ccc;
    padding-left: 15px;
    width: 100%;
    font-size: 18px;
    color: #000;
    margin-right: -1px;

    &:focus {
      border-color: darken(#ccc, 10%);
    }
  }

  @media (max-width: $mobile) {
    input {
      font-size: 16px;
    }

    input,
    .btn {
      height: 50px;
    }
  }

  @media (max-width: $mobile-xs) {
    .btn {
      min-width: auto;
    }
  }
}

// poster details

.poster-block {
  background: #20405f;
  color: #fff;
  text-align: center;
  padding: 20px 25px;
  font-family: $acrom;
  text-transform: uppercase;

  &>* {
    margin-bottom: 5px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__icon {
    margin-bottom: 15px;

    img {
      width: 60px;
      display: block;
      margin: 0 auto;
    }
  }

  &__title {
    font-size: 20px;
    font-weight: 500;
  }

  &__time {
    font-size: 23px;
    color: #bc072a;
    font-weight: 500;
  }

  &__date {
    font-size: 15px;
    font-weight: 500;
  }
}

.poster-module {
  .poster-block {
    margin-bottom: 7px;
  }

  .btn {
    width: 100%;
    font-size: 24px;
    height: 60px;
  }
}

.tickets {
  &__title {
    text-transform: uppercase;
    color: #22415e;
    font-size: 15px;
    font-weight: 500;
    font-family: $acrom;
  }

  li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }

    img {
      display: block;
      max-width: 100%;
      flex-shrink: 0;
      margin-left: 15px;

      @media (max-width: $mobile) {
        max-width: 90px !important;
      }
    }

    @media (max-width: $mobile) {
      flex-wrap: wrap;
    }
  }

  &__price {
    color: #bc072a;
    font-size: 28px;
    font-weight: 700;
    font-family: $acrom;
    text-transform: uppercase;
    position: relative;
    padding-left: 30px;

    &:before {
      content: "";
      width: 15px;
      height: 1px;
      background: #bc072a;
      display: block;
      position: absolute;
      right: 100%;
      top: 50%;
      transform: translateY(-50%) translateX(100%);
    }

    @media (max-width: $mobile) {
      font-size: 16px;
    }
  }
}

.poster-intro {
  &__image {
    img {
      display: block;
      max-width: 100%;
    }
  }

  &__bottom {
    display: flex;
    padding-right: 40px;
    margin-bottom: -60px;

    @media (max-width: $tablet) {
      margin-bottom: 0px;
    }

    @media (max-width: $mobile) {
      flex-wrap: wrap;
      padding-right: 0;
      margin-bottom: 30px;
    }
  }

  .tickets {
    max-width: 480px;

    @media (max-width: $tablet) {
      max-width: 100%;
      padding-right: 40px;
    }
  }

  .poster-module {
    max-width: 240px;
    margin-top: -120px;
    flex-shrink: 0;
    margin-left: 30px;

    @media (max-width: $mobile) {
      margin-top: 0;
      margin-left: 0;
      max-width: 100%;
      width: 100%;
    }
  }

  &__content {
    flex-grow: 1;
    padding-top: 30px;
  }

  &__title {
    text-transform: uppercase;
    color: #22415e;
    font-family: $acrom;
    font-weight: 500;
    font-size: 30px;
    margin-bottom: 90px;

    @media (max-width: $tablet) {
      margin-bottom: 30px;
    }

    @media (max-width: $mobile) {
      font-size: 24px;
    }
  }
}

