// tables
@mixin tableStyle {
    .table-wrap {
        margin-bottom: 40px;
        overflow: auto;
        width: 100%;

        &:last-child {
            margin-bottom: 0;
        }
    }

    table {
        width: 100%;

        td,
        th {
            font-weight: 400;
            border: solid 1px $c3;
            padding: 10px;
            vertical-align: middle;
        }

        &.large {
            td {
                padding: 25px;
            }
        }
    }

    @include media-breakpoint-down(lg) {
        table {
            &.large {
                td {
                    padding: 10px;
                }
            }
        }
    }
}

@include tableStyle();

// Variables

$text-color: $c3;
$quiet-color: #ccc;
$loud-color: #000;
$heading-color: #111;

$link-color: $c3;
$link-hover-color: $c3;
$link-visited-color: $c3;

$base-font-family: $noto;
$base-font-size: 14px;
$base-font-weight: 400;
$base-line-height: 1.7;

$fixed-font-family: $noto;
$fixed-font-size: 14px;
$fixed-line-height: 1.2;

$heading-font-family: $pt;
$heading-font-weight: 400;


.table-wrapper {
    width: 100%;
    display: block;
    overflow-x: auto;
}

.typography {
    letter-spacing: normal;

    &:after {
        content: '';
        display: block;
        clear: both;
    }

    ol {
        counter-reset: myCounter;

        li {
            list-style: none;
            margin-bottom: 15px;
            line-height: 1.5;

            &:last-child {
                margin-bottom: 0;
            }

            &:before {
                counter-increment: myCounter;
                content: counter(myCounter) '. ';
                color: $c10;
                font-weight: 700;
            }
        }

    }




    p {
        &:first-child {
            margin-top: 0;
        }
    }

    &__text {
        max-width: 1040px;
        margin: 0 auto;
        font-weight: 300;
    }

    &--center {
        text-align: center;
    }

    &>* {
        &:first-child {
            margin-top: 0;
        }
    }

    &>* {
        &:last-child {
            margin-bottom: 0;
        }
    }

    &--light {
        p {
            color: rgba(#000, 0.5);
        }
    }


}

/*
 * Base styles
 */

body {
    font-family: $base-font-family;
    font-weight: $base-font-weight;
}


/*
 * Typography
 */
.subtitle {
    font-size: 22px;
    font-family: $noto;
    font-weight: 100;
    line-height: 1.3;

    small {
        font-size: 80%;
    }

    @include media-breakpoint-down(sm) {
        font-size: 18px;
    }
}

.title {
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    font-family: $noto;
    line-height: 1.4;
    letter-spacing: 3.15px;

    small {
        font-size: 80%;
    }

    @include media-breakpoint-down(sm) {
        font-size: 26px;
    }

    @include media-breakpoint-down(xs) {
        font-size: 22px;
    }
}

.link,
a {
    color: $c10;

    // &:visited {
    //     color: $link-visited-color;
    // }

    // &:active {
    //     color: $link-color;
    // }

    &:hover {
        text-decoration: none;
        //color: $link-hover-color;
    }

}

.fixed {
    font-family: $fixed-font-family;
    font-size: $fixed-font-size;
    line-height: $fixed-line-height;
}

.quiet {
    font-color: $quiet-color;
}

.loud {
    font-color: $loud-color;
}

.italic {
    font-style: italic;
}

.bold,
strong,
b {
    font-weight: 700;
    @extend .loud;
}

.ttu {
    text-transform: uppercase;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
    font-weight: $heading-font-weight;
    font-family: $noto;

    &:first-child {
        margin-top: 0;
    }
}

.h1 {
    line-height: 1.3;
}

.h2 {
    line-height: 1.4;
}

.h3,
.h4 {
    text-transform: uppercase;
    line-height: 1.3;
}

.h1 {
    font-size: 180%;
    font-weight: 700;
}

.h2 {
    font-size: 160%;
    font-weight: 700;
}

.h3 {
    font-size: 145%;
    font-weight: 600;
}

.h4 {
    font-size: 100%;
    font-weight: 500;
}


.block-margins {
    margin: 1.3em 0;
}

.unordered-list {
    padding-left: 0;
    margin-bottom: 2em;

    li {
        position: relative;
        padding-left: 30px;
        margin-bottom: 15px;
        line-height: 1.5;

        &:last-child {
            margin-bottom: 0;
        }

        &:before {
            content: '';
            width: 14px;
            height: 2px;
            display: block;
            background: $c10;
            position: absolute;
            top: .5em;
            left: 0;
        }

    }
}

.strong-list {
    font-weight: 700;
    margin-bottom: 2em;

    li {
        &:before {

            height: 2px !important;
            top: .55em !important;
        }
    }
}

.ordered-list {
    list-style: decimal;
    margin-bottom: 2em;
    padding-left: 30px;
}

.typography {
    color: #000;
    font-family: $noto;
    font-size: $base-font-size;
    line-height: $base-line-height;
    text-align: justify;

    a {
        @extend .link;
    }

    i,
    em {
        @extend .italic;
    }

    b,
    strong {
        @extend .bold;
    }

    h1,
    .h1 {
        @extend .h1;
        margin: 1em 0;
    }

    h2,
    .h2 {
        @extend .h2;
        margin: 1em 0;
    }

    h3,
    .h3 {
        @extend .h3;
        margin: 1em 0;
    }

    h4,
    .h4 {
        @extend .h4;
        margin: 1em 0;
    }

    h5,
    .h5 {
        @extend .h5;
        margin: 1em 0;
    }

    h6,
    .h6 {
        @extend .h6;
        margin: 1em 0;
    }

    p,
    ul,
    ol,
    pre {
        @extend .block-margins;

        &:first-child {
            margin-top: 0;
        }
    }

    ul {
        @extend .unordered-list;
    }

    ol {
        @extend .ordered-list;
    }

    pre,
    code {
        @extend .fixed;
    }

    img {
        max-width: 100%;
    }

    big {
        font-size: 115%;
    }

    @media (max-width: $mobile) {
        img {
            margin: 1em auto !important;
            width: 100%;
        }

        text-align: left;
        font-size: 13px !important;
        line-height: 1.6;
    }
}


/*
 * Modifiers
 */

.float-left {
    float: left;
    margin-right: 1em;
}

.float-right {
    float: right;
    margin-left: 1em;
}

.text-left {
    text-align: left !important;
}

.text-center {
    text-align: center !important;
}

.text-right {
    text-align: right !important;
}

// section modules title and desc 

.module-title {
    @extend .h2;
    margin-bottom: 30px;

    &--strong {
        font-weight: 500;
    }
}

.module-desc {
    @extend .h3;
}

.list-checks {
    li {
        &:before {
            content: '';
            width: 18px !important;
            height: 13px !important;
            background: url('../img/icons/check.svg') center no-repeat !important;
            background-size: contain !important;
            top: .2em !important;
        }
    }
}