.loader {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;

  &__inside {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

.spinner {
  animation: spin 1.5s linear infinite;
  position: relative;
  width: 120px;
  height: 120px;
  border: 2px solid #bf1026;
  border-radius: 50%;

  @media (max-width: $tablet-sm) {
    width: 70px;
    height: 70px;
  }

  &:after {
    content: "";
    width: 85%;
    height: 85%;
    background: -webkit-linear-gradient(45deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 40%, rgba(255, 255, 255, 0.7) 60%, rgba(255, 255, 255, 0) 100%);
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(-5%, -5%);
  }

}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}