.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99999;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: rgba(#000, 0.8);
    background-size: cover;
    transition: opacity 0.3s linear;
    opacity: 0;
    visibility: hidden;
    padding: 0 70px;
    overflow: auto;
    flex-flow: row wrap;
    display: none;

    &.init {
        display: flex;
    }

    .modal-block {
        opacity: 0;
        transition: all 0.5s ease;
        transform: translate(-10px, 0) scale(0.95);
    }

    &.active {
        opacity: 1;
        transform: none;

        .modal-block {
            opacity: 1;
            transform: none;
        }

        visibility: visible;
    }

    @include media-breakpoint-down(sm) {
        padding-left: 0;
        padding-right: 0;

    }
}

.modal-block {
    width: 100%;
    max-width: 710px;
    margin-bottom: 65px;
    margin-top: 65px;
    background: #fff;
    color: #000;
    padding: 30px 60px;
    position: relative;

    &__close {
        width: 28px;
        height: 28px;
        cursor: pointer;
        background: url('../img/icons/cross.svg') center no-repeat;
        background-size: contain;
        display: block;
        position: absolute;
        right: 0;
        top: -50px;
        transition: all .2s ease;

        &:hover {
            opacity: 0.9;
            transform: scale(0.95);
        }

        @include media-breakpoint-down(sm) {
            right: 15px;
        }
    }

    &__title {
        margin-bottom: 30px;
        font-size: 32px;
        text-transform: uppercase;
        text-align: center;
        font-weight: 700;
    }

    @include media-breakpoint-down(sm) {
        padding: 30px 30px;

        &__title {
            font-size: 24px;
        }
    }

    @include media-breakpoint-down(xs) {
        .btn {
            font-size: 15px;
            width: 100%;
        }
    }
}

#more-events {
    .modal-block {
        max-width: 500px;

        &__title {
            .date {
                font-size: 0.5em;
                font-weight: 400;
                margin-top: 5px;
            }
        }
    }

    .calendar-events {}

    .calendar-event {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 15px;
        padding-bottom: 15px;
        padding-top: 15px;
        border-bottom: 1px solid #f1f1f1;

        &:last-child {
            margin-bottom: 0;
            border: none;
        }

        &__title {
            font-weight: 700;
            flex-grow: 1;
            text-transform: uppercase;
            text-align: left;
            margin-right: 15px;
        }

        &__time {
            color: #bc072a;
            font-size: 17px;
        }

        img {
            width: 40px;
            margin-right: 15px;
        }
    }
}