//colors
$color_alto_approx: #20405f;
$color_moody_blue_approx: $c10;
$color_concrete_approx: #f1f2f3;
$white: #fff;
$color_willow_brook_approx: #dff1de;
$color_link_water_approx: #dddffb;

#calendar {
  max-width: 1200px;
  font-family: $acrom;

  >* {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    width: 100%;
  }

  .calendar-head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0.5em 0;

    h2 {
      width: auto;
      margin: 0;
    }

    .calendar-trigger {
      width: 60px;
      height: 40px;
      //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
      border-radius: 0.25em;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      border: 1px solid $color_alto_approx;
    }
  }

  .calendar-days {
    text-align: center;
    border-bottom: 3px solid $color_alto_approx;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 12px;

    >* {
      padding: 1em 0;
      color: $color_alto_approx;

      &:nth-child(6) {
        color: $c10;
      }

      &:nth-child(7) {
        color: $c10;
      }
    }
  }

  .more-events {
    //position: absolute;
    //bottom: 30px;
    //left: 0;
    text-align: center;
    font-size: 12px;
    //right: 0;
    margin-top: 5px;
    text-decoration: underline;
    display: none;

    &.show {
      display: block;
    }

    &:hover {
      color: #000;
    }
  }

  .has-events {
    background: #fff !important;
  }

  .calendar-dates {
    grid-column-gap: 1px;
    grid-row-gap: 1px;
    background: $color_alto_approx;
    border-left: 1px solid $color_alto_approx;
    border-bottom: 1px solid $color_alto_approx;
    border-right: 1px solid rgb(32, 64, 95);

    >* {
      position: relative;
      overflow: hidden;
      z-index: 1;
      background: rgb(243, 246, 250);

      &:focus {
        outline: 1px solid $color_moody_blue_approx;
      }

      .calendar-number {
        position: absolute;
        bottom: 10px;
        right: 0px;
        left: 0;
        color: #22415e;
        font-family: $acrom;
        font-size: 11px;
        font-weight: 500;
        text-transform: uppercase;
        text-align: center;
        line-height: 1;
        z-index: 1;

        @media (max-width: $tablet-sm) {
          font-size: 9px;
        }

        @media (max-width: $mobile) {
          bottom: 0;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }

      .calendar-events {
        // position: absolute;
        // bottom: 0px;
        // height: 100%;
        // left: 0;
        // right: 0;
        font-size: 0.875rem;
        text-align: center;

        img {
          width: 50px;
          display: block;
          margin: 0 auto 10px;

          @media (max-width: $tablet) {
            width: 30px;
          }

          @media (max-width: $tablet-sm) {
            display: none;
          }
        }

        .calendar-event {
          flex-direction: column;
          align-items: center;
          justify-content: center;
          background: #fff;
          margin: 1px 2px;
          padding: 10px 4px;
          padding-bottom: 30px;
          height: 100%;
          display: none;

          &:first-child {
            display: flex;
            margin-top: -100%;
          }

          @media (max-width: $mobile) {
            padding-bottom: 0;
          }

          &__title {
            display: block;
            font-size: 15px;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: -0.3px;
            color: #22415e;
            margin-bottom: 5px;
          }

          &__time {
            font-size: 17px;
            color: $c10;
            font-weight: 700;
            text-transform: uppercase;
          }

          @media (max-width: $tablet) {

            &__title,
            &__time {
              font-size: 14px;
            }

          }

          @media (max-width: $tablet-sm) {

            &__title,
            &__time {
              font-size: 11px;
            }


          }
        }
      }
    }

    >*::before {
      content: "";
      display: block;
      padding-top: 100%;
    }

    > {
      .prev-date {
        span {
          opacity: 0.3;
        }
      }

      .next-date {
        span {
          opacity: 0.3;
        }
      }

      .today-date {
        background: rgb(231, 237, 244);
      }

      // .calendar-active {
      //   background: rgba(231, 237, 244, 0.2);
      // }
    }
  }
}

#calendar-active {
  text-align: right;
  opacity: 0.5;
}

@media (max-width: $tablet-sm) {
  #calendar {
    .calendar-dates {
      border-right: 1px solid $color_alto_approx;

      >* {
        .calendar-number {
          top: 50%;
          right: 50%;
          //Instead of the line below you could use @include transform($scale, $rotate, $transx, $transy, $skewx, $skewy, $originx, $originy)
          transform: translate3d(50%, -50%, 0);
          font-size: 10px;
          bottom: auto;

          .month {
            display: none;
          }
        }

        .more-events {
          position: absolute;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
          margin: 0;
          text-decoration: none;
          width: 100%;
          height: 100%;
          display: block;

          strong {
            background: #bc072a;
            color: #fff;
            font-size: 10px;
            width: 16px;
            font-weight: 500;
            height: 16px;
            line-height: 16px;
            border-radius: 50%;
            display: block;
            text-align: center;
            position: absolute;
            top: 2px;
            right: 2px;
          }

          span {
            display: none;
          }
        }

        .calendar-events {
          top: 0px;
          right: 0;
          bottom: 0;
          left: 0;
          position: absolute;
          z-index: 5;

          .calendar-event {
            &>*:not(.more-events) {
              display: none !important;
            }

            &__time {
              position: absolute;
              top: 0;
              font-size: 9px;
              line-height: 0.7;
              right: 0;
              background: $c10;
              color: #fff;
              border-radius: 5px;
              padding: 2px;
            }
          }
        }
      }
    }

    .calendar-days>* {
      padding: 0.25em 0;
      font-size: 10px;
      @include truncate(90%);
    }

  }
}

button {
  border: none;
}