* {
	box-sizing: border-box; }

html,
body {
	min-height: 100%; }

body {
	background: #fff;
	color: #3f3f3f;
	font-size: $fz;
	font-family: $pt;
	line-height: 1.4;
	min-width: 320px;
	overflow-x: hidden;
	@include media-breakpoint-down(md);
	&.overflow {
		overflow: hidden;
		@include media-breakpoint-down(lg);
		&.overflow-bg {
			overflow: hidden;
			&:before {
				content: '';
				background: rgba(#02273E, 0.84);
				width: 100%;
				height: 100%;
				left: 0;
				right: 0;
				bottom: 0;
				top: 140px;
				position: fixed;
				z-index: 20;
				@include media-breakpoint-down(sm);
				font-size: 14px; } } } }

// выделение текста
::selection {
	background-color: #20405f;
	color: $white; }

.section {
	margin: 45px 0; }

.text-right {
	text-align: right; }

.text-left {
	text-align: left; }
